import React,{ useState,useEffect }from 'react'
import {Link} from 'react-router-dom'
import {useDispatch, useSelector} from 'react-redux'
import {Row, Col , Image , ListGroup, Card, Button, Form , option} from 'react-bootstrap'
import Rating from '../components/Rating'
import Loader from '../components/Loader'
import Meta from '../components/Meta'
import Message from '../components/Message'

import {listProductDetails,createProductReview} from '../actions/productActions'
import {PRODUCT_CREATE_REVIEW_RESET} from '../constants/productConstants'

const ProductScreens = ({history,match}) => {

    const [qty,setQty]= useState(1)
    const [rating,setRating]= useState(0)
    const [comment,setComment]= useState('')

    const dispatch = useDispatch()

    const productDetails= useSelector(state=> state.productDetails)
    const {loading,error,product}=productDetails

    const userLogin= useSelector(state=> state.userLogin)
    const {userInfo}=userLogin

    const productReviewCreate= useSelector(state=> state.productReviewCreate)
    const {success:successProductReview,error:errorProductReview}=productReviewCreate

    useEffect(()=>{
         if(successProductReview){
             alert('Review Submitted')
             setRating(0)
             setComment('')
             dispatch({type:PRODUCT_CREATE_REVIEW_RESET})
         }

        dispatch(listProductDetails(match.params.id))
    }, [dispatch,match,successProductReview]) 
    
    const addToCartHandler= () =>{
        history.push(`/cart/${match.params.id}?qty=${qty}`)
    }

    const submitHandler= (e) =>{
        e.preventDefault()
        dispatch(createProductReview(match.params.id,{
            rating,
            comment
        }))
    }

    return (
        <>
           <Link className="btn btn-dark my-3" to="/products">
               Go Back
           </Link>
            {loading? (<Loader />) : error? (<Message variant='danger'>{error}</Message>):( 
                <>
                <Meta title={product.name} />
                <Row>
               <Col md={6}>
                <Image className="img-screens"src={product.image} alt={product.name} fluid />
               </Col>
               <Col md={3}>
                  <ListGroup variant='flush'>
                   <ListGroup.Item>
                       <h2>{product.name}</h2>
                   </ListGroup.Item>
                 <ListGroup.Item>
                     <Rating value={product.rating} text={`${product.numReviews} reviews `} />
                 </ListGroup.Item>
                 <ListGroup.Item>
                  <h3> Price:  {product.price} </h3>
                 </ListGroup.Item>
                 <ListGroup.Item>
                   Description: {product.description}
                 </ListGroup.Item>
                  </ListGroup>
               </Col>
               <Col md={3}>
                   <Card>
                       <ListGroup variant='flush'>
                           <ListGroup.Item>
                               <Row>
                                   <Col>
                                       Price:
                                   </Col>
                                   <Col>
                                       <strong> {product.price}</strong>
                                   </Col>
                               </Row>
                           </ListGroup.Item> 
                           <ListGroup.Item>
                               <Row>
                                   <Col>
                                       Status:
                                   </Col>
                                   <Col>
                                       {product.countInStock>0 ? 'In Stock': 'Out Of Stock'}
                                   </Col>
                               </Row>
                           </ListGroup.Item>  
                           {product.countInStock>0 &&(
                               <ListGroup.Item>
                                   <Row>
                                       <Col>
                                           Qty
                                       </Col>
                                       <Col>
                                           <Form.Control as='select' value={qty} onChange={(e)=>
                                            setQty(e.target.value)}
                                            >
                                            {
                                             [...Array(product.countInStock).keys()].map((x) =>(
                                                <option key={x+1} value={x+1}>
                                                    {x+1}
                                                </option>
                                            ))}
                                           </Form.Control>
                                       </Col>
                                   </Row>
                               </ListGroup.Item>
                           )} 
                           <ListGroup.Item>
                                    <Button  onClick={addToCartHandler} className='btn-block' type='button' disabled={product.countInStock===0}>
                                           Add To Cart
                                    </Button>
                           </ListGroup.Item>
                       </ListGroup>
                   </Card>
                   <Card className="my-3 p-3 rounded">
                   <h3> </h3>
        <Link to={`/product/${product._id}/seller`}>
            <Card.Img className="img-Change"src={product.sellerImage} variant="top" />
        </Link>
        <Card.Body>
          <Link to={`/product/${product._id}/seller`}>
            <Card.Title as='div'>
                <strong>{product.sellerName}</strong>
            </Card.Title>
          </Link>
        <Card.Text as="div">
         <strong>Contact:</strong> {product.sellerContact}
        </Card.Text>
        <br />
        <Link to={`/product/${product._id}/seller`}>
            <Card.Text as='div'>
                <strong>Read More</strong>
            </Card.Text>
        </Link>
        </Card.Body>
        </Card>
               </Col>
           </Row>
          <Row>
               <Col md={6}>
                   <h2>Reviews</h2>
                   {product.reviews.length===0 && <Message>No Reviews</Message>} 
                   <ListGroup variant='flush'>
                      {product.reviews.map(review => (
                          <ListGroup.Item key={review._id}>
                             <strong>{review.name}</strong>
                             <Rating value={review.rating} />
                             <p>{review.createdAt.substring(0,10)}</p>
                             <p>{review.comment}</p>
                          </ListGroup.Item>
                      ))}
                      <ListGroup.Item>
                          <h2>Write a Customer Review</h2>
                          {errorProductReview && <Message variant='danger'>{errorProductReview}</Message>}
                          {userInfo? (
                              <Form onSubmit={submitHandler}>
                                  <Form.Group controlId='rating'>
                                      <Form.Label>Rating</Form.Label>
                                      <Form.Control as='select' value={rating} onChange={(e) => setRating(e.target.value)}>
                                          <option value=''>Select...</option>
                                          <option value='1'>1 - Poor</option>
                                          <option value='2'>2 - Fair</option>
                                          <option value='3'>3 - Good</option>
                                          <option value='4'>4 - Very Good</option>
                                          <option value='5'>5 - Excellent</option>
                                      </Form.Control>
                                  </Form.Group>
                                  <Form.Group controlId='comment'>
                                    <Form.Label>Comment</Form.Label>
                                    <Form.Control as='textarea' row='3' value={comment} onChange={(e) => setComment(e.target.value)}>
                                    </Form.Control>
                                  </Form.Group>
                                  <br />
                                  <Button type='submit' variant='primary'>
                                      Submit
                                  </Button>
                              </Form>
                          )
                          : <Message>Please <Link to='/login'>Sign In</Link> to post a Review</Message>}
                      </ListGroup.Item>
                   </ListGroup>
               </Col>
          </Row>
          </>
            )}            
        </>
    )
}

export default ProductScreens
